import Vue from "vue";
import Router from "vue-router";
import { Home, Section, Question, Form, Translation, Answer } from "./views";
import { define } from "./resources/ability";
import { path, propEq, find } from "ramda";

const requireAuth = (to, from, next) => {
  const abilities = define();
  const permission = path(["meta", "permission"], to);
  if (permission && abilities.cannot(permission[0], permission[1])) {
    const redirect = path(["meta", "redirect"], to);
    const page = find(propEq("actions", "page"), abilities.rules);
    if (redirect) {
      return next(redirect);
    } else if (page) {
      alert("Nemte dostaten oprvnn");
      return next("/");
    }
    return next("/fe/login");
  }
  return next();
};

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.NODE_ENV === "production" ? "/admin" : "/",
  routes: [
    {
      path: "/home",
      name: "home",
      component: Home
      // beforeEnter: requireAuth,
      // meta: {
      //   permission: ["page", "Home"]
      // }
    },
    {
      path: "/",
      name: "question",
      component: Question
      // beforeEnter: requireAuth,
      // meta: {
      //   permission: ["page", "Home"]
      // }
    },
    {
      path: "/sections",
      name: "section",
      component: Section
      // beforeEnter: requireAuth,
      // meta: {
      //   permission: ["page", "Home"]
      // }
    },
    {
      path: "/forms",
      name: "form",
      component: Form
      // beforeEnter: requireAuth,
      // meta: {
      //   permission: ["page", "Home"]
      // }
    },
    {
      path: "/translation",
      name: "translation",
      component: Translation
      // beforeEnter: requireAuth,
      // meta: {
      //   permission: ["page", "Home"]
      // }
    },
    {
      path: "/answers",
      name: "answers",
      component: Answer
      // beforeEnter: requireAuth,
      // meta: {
      //   permission: ["page", "Home"]
      // }
    },
    {
      path: "/fe/login",
      name: "login",
      component: () => import("./views/Login")
    },
    {
      path: "/fe/logout",
      name: "logout",
      beforeEnter: (to, from, next) => {
        localStorage.basicToken = "";
        location.href = "/";
      }
    }
  ]
});
